    <div class="filterBox">
      <img class="filterBg" src="@/assets/images/banner.png" alt="">
      <div class="searchCard">
        <div class="searchBox">
          <div class="dateBox">
            <span>20</span>
            <input type="text" name="" v-model="year" maxlength="2">
            <span>年</span>
            <input type="text" name="" v-model="month" maxlength="2">
            <span>月</span>
            <input type="text" name="" v-model="day" maxlength="2">
            <span>日</span>
            <input type="text" name="" v-model="startHour" maxlength="2"><span>:</span><input type="text" name="" v-model="startMinute" maxlength="2"><span> - </span><input type="text" name="" v-model="endHour" maxlength="2"><span>:</span><input
              type="text" name="" v-model="endMinute" maxlength="2">
          </div>
          <div class="personBox">
            <input type="text" name="" value="" placeholder="可容人数">
            <span>人</span>
          </div>
          <div class="addressBox">
            <img src="@/assets/images/address.png" alt="">
            <input type="text" name="" value="">
          </div>
        </div>
        <a href="#">搜索</a>
      </div>
      <div class="clockBox">
        <div :class="['shadow', hourShadow > 6 ? 'shadow' + 6 : 'shadow' + hourShadow ]" :style="{'transform': 'rotate(' + hourStartDeg + 'deg)'}">
          <span></span>
        </div>
        <div :class="['shadow', 'shadow' + (hourShadow - 6) ]" :style="{'transform': 'rotate(' + (hourStartDeg+180) + 'deg)'}">
          <span></span>
        </div>
        <div :class="['shadow', minuteShadow > 30 ? 'minuteShadow' + 30 : 'minuteShadow' + minuteShadow ]" :style="{'transform': 'rotate(' + minuteStartDeg + 'deg)'}">
          <span></span>
        </div>
        <div :class="['shadow', 'minuteShadow' + (minuteShadow - 30) ]" :style="{'transform': 'rotate(' + (minuteStartDeg+180) + 'deg)'}">
          <span></span>
        </div>
        <img class="hourHand" src="@/assets/images/hourHand.png" :style="{'transform': 'rotate(' + hourEndDeg + 'deg)'}">
        <img class="minuteHand" src="@/assets/images/minuteHand.png" :style="{'transform': 'rotate(' + (minuteEndDeg-90) + 'deg)'}">
      </div>
