</template>

<script>
  // import {
  //   getVisitorDetailById,
  //   transformToBase64
  // } from "../service/getData";
  const myDate = new Date();

  export default {
    name: 'index',
    data() {
      return {
        year: Number(myDate.getFullYear()) - 2000,
        month: Number(myDate.getMonth()) + 1,
        day: myDate.getDate(),
        startHour: 0,
        startMinute: 0,
        endHour: 0,
        endMinute: 0,
        hourStartDeg: 0,
        hourEndDeg: 0,
        minuteStartDeg: 0,
        minuteEndDeg: 0,
        hourShadow: 0,
        minuteShadow: 0
      }
    },
    created() {

    },
    methods: {
      // getVisitorDetailById: function() {
      //   getVisitorDetailById(this.$route.params.id).then(res => {
      //     if (res.res.code === 10000) {
      //       this.details = res.body.visitorOutput
      //       this.transformToBase64()
      //     }
      //   })
      // },
    },
    watch: {
      startHour: function(value) {
        if (value > 23) {
          this.startHour = 23
        }
        this.hourStartDeg = (value / 12) * 360
        this.hourShadow = this.endHour - value
      },
      startMinute: function(value) {
        if (value > 59) {
          this.startMinute = 59
        }
        this.minuteStartDeg = (value / 60) * 360
      },
      endHour: function(value) {
        this.hourEndDeg = (value / 12) * 360
        if (value > 23) {
          this.endHour = 23
        }
        if (value - this.startHour > 12) {
          this.hourShadow = 12
        } else {
          this.hourShadow = value - this.startHour
        }
      },
      endMinute: function(value) {
        this.minuteEndDeg = (value / 60) * 360
        if (value > 59) {
          this.endMinute = 59
        }
        if (value - this.startMinute > 60) {
          this.minuteShadow = 60
        } else {
          this.minuteShadow = value - this.startMinute
        }
      }
    }
  }
</script>

